@import url(https://fonts.googleapis.com/css2?family=Lato:wght@300;400;700&display=swap);
.generator-form,
.generator-result {
  margin: 0 auto;
}

.generator-form {
  width: 80%;
  display: -webkit-flex;
  display: flex;
}

.generator-result {
  width: 60%;
  -webkit-align-self: center;
          align-self: center;
  -webkit-animation: fadeIn 500ms ease-in forwards;
          animation: fadeIn 500ms ease-in forwards;
}

.generator-result-img {
  width: 100%;
  max-width: 400px;
}

.generator-result-text {
  font-size: 12px;
}

@media screen and (min-width: 992px) {
  .generator-form {
    width: 50%;
  }
}

@media screen and (max-width: 576px) {
  .generator-form {
    -webkit-flex-direction: column;
            flex-direction: column;
  }

  .generator-button {
    margin: 10px 0;
  }
}

@-webkit-keyframes fadeIn {
  from {
    opacity: 0;
  }

  to {
    opacity: 1;
  }
}

@keyframes fadeIn {
  from {
    opacity: 0;
  }

  to {
    opacity: 1;
  }
}

.reader-content {
  width: 80%;
  max-width: 400px;
  margin: 0 auto;
  -webkit-animation: fadeIn 400ms ease-in forwards;
          animation: fadeIn 400ms ease-in forwards;
}

.reader-result {
  -webkit-animation: slideRight 200ms ease-in forwards;
          animation: slideRight 200ms ease-in forwards;
}

.reader-no-result,
.reader-result {
  -webkit-align-self: center;
          align-self: center;
  padding: 5%;
}

.reader-result-text {
  font-size: 24px;
  font-weight: 700;
}

.reader-result > h6 {
  font-size: 14px;
}

@media screen and (min-width: 1200px) {
  .reader-content {
    width: 30%;
  }
}

@media screen and (max-width: 992px) {
  .reader-result-text {
    font-size: 20px;
  }

  .reader-result > h6 {
    font-size: 12px;
  }
}

@-webkit-keyframes fadeIn {
  from {
    opacity: 0;
  }

  to {
    opacity: 1;
  }
}

@keyframes fadeIn {
  from {
    opacity: 0;
  }

  to {
    opacity: 1;
  }
}

@-webkit-keyframes slideRight {
  from {
    -webkit-transform: translateX(-100%);
            transform: translateX(-100%);
  }

  to {
    -webkit-transform: translateX(0);
            transform: translateX(0);
  }
}

@keyframes slideRight {
  from {
    -webkit-transform: translateX(-100%);
            transform: translateX(-100%);
  }

  to {
    -webkit-transform: translateX(0);
            transform: translateX(0);
  }
}

.App {
  display: grid;
  grid-template-rows: auto auto 1fr auto;
  -webkit-align-items: center;
          align-items: center;
  justify-items: center;
  text-align: center;
  width: 100%;
  height: 100vh;
}

.main-content {
  width: 100%;
  height: 100%;
  display: grid;
  grid-template-rows: auto 1fr;
}

/* HEADER */
.App-footer,
.App-header {
  width: 100%;
}

.App-header {
  background-color: #2a2b2b;
  margin-bottom: 20px;
  padding: 20px;
}

.App-header > h1 {
  color: #f3cc41;
  font-weight: bold;
  font-size: 24px;
}

/* FOOTER */
.App-footer {
  margin-top: auto;
  padding: 10px;
}

.footer-text {
  font-size: 12px;
}

@media screen and (min-width: 576px) {
  .App-header > h1 {
    font-size: 32px;
  }
}

body {
  margin: 0;
  font-family: "Lato", sans-serif, "Ubuntu", "Roboto";
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

